import { getList } from './util'

// eslint-disable-next-line import/no-anonymous-default-export
export default {
    namespace: 'data',
    state: {
        sizes: {
            'XS': true,
            'S': true,
            'M': true,
            'ML': true,
            'L': true,
            'XL': true,
            'XXL': true
        },              // 商品尺码
        data: null,     // 商品数据
        shopCart: [],   // 购物车
        isEject: false, // 购物车是否弹出
    },
    effects: {
        // 获取商品数据
        *getListHttp(_, { call, put }) {
            const result = yield call(getList)
            yield put({
                type: 'getData',
                payload: {
                    data: result.products
                }
            })
        },
        // 添加商品
        *addGood({ payload: item }, {_, put, select}) {
            const tempArr = yield select(state => {
                return JSON.parse(JSON.stringify(state.data.shopCart))
            })
            if (tempArr.length !== 0) {
                let temp = null
                for (let i in tempArr) {
                    if (tempArr[i].id === item.id) {
                        tempArr[i].quantity++
                        temp = i
                        break
                    }
                }
                if (temp === null) {
                    item["quantity"] = 1
                    tempArr.push(item)
                }
            } else {
                item["quantity"] = 1
                tempArr.push(item)
            }
            yield put({
                type: 'addCart',
                payload: tempArr
            })
        },
        // 删除商品
        *deleteCart({ payload: id }, {_, put, select}) {
            const tempArr = yield select(state => {
                return JSON.parse(JSON.stringify(state.data.shopCart))
            })
            for (let i in tempArr) {
                if (tempArr[i].id === id) {
                    tempArr.splice(i, 1)
                    break
                }
            }
            yield put({
                type: 'deleteCarts',
                payload: tempArr
            })
        },
        // 减少一件
        *subtrackGood({ payload: id }, {_, put, select}) {
            const tempArr = yield select(state => {
                return JSON.parse(JSON.stringify(state.data.shopCart))
            })
            for (let i in tempArr) {
                if (tempArr[i].id === id) {
                    if (tempArr[i].quantity - 1 === 0) {
                        tempArr.splice(i, 1)
                    } else {
                        tempArr[i].quantity--
                    }
                    break
                }
            }
            yield put({
                type: 'subtrack',
                payload: tempArr
            })
        },
        // 增加一件
        *addOneGood({ payload: id }, {_, put, select}) {
            const tempArr = yield select(state => {
                return JSON.parse(JSON.stringify(state.data.shopCart))
            })
            for (let i in tempArr) {
                if (tempArr[i].id === id) {
                    tempArr[i].quantity++
                    break
                }
            }
            yield put({
                type: 'add',
                payload: tempArr
            })
        },
    },
    reducers: {
        getData(state, { payload: data }) {
            // console.log('getData', state);
            return { ...state, ...data }
        },
        // 获取购物车数据
        getShopCart(state, { payload: shopCart }) {
            // console.log(shopCart);
            return { ...state, shopCart }
        },
        get(state) {
            // console.log('get', state);
            return { ...state }
        },
        // 过滤尺码
        filterSize(state, { payload: sizes }) {
            // console.log('filterSize', sizes)
            return { ...state, ...sizes }
        },
        // 排序
        sort(state, { payload: data }) {
            // console.log('sort', data);
            return { ...state, ...data }
        },
        // 添加商品
        addCart(state, { payload: tempArr }) {
            // console.log('addCart', tempArr)
            return {
                ...state,
                shopCart: tempArr
            }
        },
        // 删除商品
        deleteCarts(state, { payload: shopCart }) {
            // console.log('deleteCarts', shopCart);
            return { ...state, shopCart }
        },
        // 减少一件
        subtrack(state, { payload: shopCart }) {
            // console.log('subtrack', shopCart);
            return { ...state, shopCart }
        },
        // 添加一件
        add(state, { payload: shopCart }) {
            // console.log('add', shopCart);
            return { ...state, shopCart }
        },
        // 弹出购物车
        eject(state) {
            // console.log('eject');
            return {
                ...state,
                isEject: true
            }
        },
        // 关闭购物车
        closeShop(state) {
            // console.log('closeShop');
            return {
                ...state,
                isEject: false
            }
        }
    },
}